@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100;200;300;400;500;600;700;800;900&display=swap');

* {
    margin: 0;
    padding: 0;
    border: 0;
    line-height: 1.4em;
    vertical-align: baseline;
    text-decoration: none;
    outline: 0;
    box-sizing: border-box;
    background-color: transparent;
    font-family: 'Montserrat', sans-serif;
}

$resolution: 2560px;

$height-sec-1: 830px;
$height-sec-2: 1100px;
$height-sec-3: 1000px;
$height-sec-4: 300px;

$height-quick-contact: 270px;

$l-pt-quick-contact: 1555px;

$r1-pt-home-1: 1810px;
$l2-pt-home-1: 120px;

$l2-pt-home-2: 1820px;

$r1-pt-home-3: 1810px;
$l2-pt-home-3: 120px;

$r1-pt-home-4: 120px;
$l2-pt-home-4: 1820px;

$r1-pt-about-1: 1810px;
$l2-pt-about-1: 300px;

$r1-pt-about-2: 0px;
$l2-pt-about-2: 1820px;

$l1-pt-about-3: 550px;
$r1-pt-about-3: 1920px;

$l2-pt-about-3: 1680px;
$r2-pt-about-3: 2450px;

$r1-pt-about-4: 820px;
$l2-pt-about-4: 950px;

$l1-pt-services-1: 420px;
$r1-pt-services-1: 1670px;

$l1-pt-services-2: 410px;
$r1-pt-services-2: 840px;

$l2-pt-services-3: 200px;
$x2-pt-services-3: 200px;

$r1-pt-contact-1: 1600px;
$l1-pt-contact-2: 0px;

header {
    width: 100%;
    height: 195px;
    background-color: black;
    font-weight: 400;
    display: flex;
    .head-content {
        width: $resolution;
        height: 100%;
        margin: 0 auto;
        padding: 0 200px;
        display: flex;
        justify-content: space-between;
        .logo {
            height: 100%;
            display: flex;
            align-items: center;
            img {
                height: 70px;
            }
        }
        .menu {
            height: 100%;
            font-size: 30px;
            display: flex;
            flex-grow: 1;
            justify-content: right;
            .menu-items {
                max-width: 950px;
                list-style: none;
                display: flex;
                flex-grow: 1;
                justify-content: space-around;
                align-items: center;
                li {
                    border-bottom: 4px transparent solid;
                    padding: 7px 10px;
                    &.active {
                        border-bottom: 4px white solid;
                    }
                    a {
                        color: white;
                    }
                }
            }
            .languages {
                width: 240px;
                margin-left: 60px;
                list-style: none;
                display: flex;
                justify-content: space-around;
                align-items: center;
                li {
                    margin: 2px;
                    padding: 5px 7px;
                    &.active {
                        background-color: white;
                        a {
                            color: black;
                            font-weight: 700;
                        }
                    }
                }
                a {
                    color: white;
                }
            }
        }
        .hamburger {
            display: none;
            width: 20px;
            height: 20px;
            margin-right: 20px;
        }
    }
}

.container {
    width: $resolution;
    min-height: 100vh;
    margin: 0 auto;
}

.background {
    position: absolute;
    width: $resolution;
    margin: 0 auto;
    z-index: -2;
}

.polygons {
    position: absolute;
    left: 0; 
    width: 100%;
    .shadow {
        position: absolute;
        top: 0;
        width: 100%;
        height: 100%;
        .polygon {
            position: absolute;
            top: 0;
            width: 100%;
            height: 100%;
            &.white {
                background-color: white;
            }
            &.black {
                background-color: black;
            }
        }
    }
}

.polywrapper {
    position: relative;
    width: $resolution;
    height: $height-sec-1;
    margin: 0 auto;
}

.content {
    position: absolute;
    width: $resolution;
    margin: 0 auto;
    &.black {
        color: black;
        span {
            border-bottom: black 5px solid;
        }
    }
    &.white {
        color: white;
        .subtitle {
            font-weight: 400;
        }
        span {
            border-bottom:  white 4px solid;
        }
    }

    h1 {
        font-size: 85px;
        font-weight: 300;
        line-height: 80px;
        text-transform: uppercase;
        span {
            display: inline-block;
            min-width: 650px;
            padding-bottom: 40px;
            font-weight: 600;
            line-height: 80px;
        }
    }

    h2 {
        font-size: 54px;
        font-weight: 700;
        line-height: 40px;
        text-transform: uppercase;
        margin-bottom: 50px;
    }

    .text {
        font-size: 24px;
        font-weight: 500;
        line-height: 36px;
        margin-bottom: 50px;
    }

    .subtitle {
        margin: 60px 0;
        font-size: 24px;
        font-weight: 800;
    }

    .button {
        min-width: 300px;
        width: max-content;
        padding: 16px 30px;
        margin: 20px 0;
        font-size: 25px;
        font-weight: 600;
        display: flex;
        justify-content: space-around;
        align-items: center;
        &.white {
            background-color: black;
            color: white;
        }
        &.black {
            background-color: white;
            color: black;
            font-weight: 800;
        }
        img {
            height: 36px;
            margin-left: 40px;
        }
    }
}

.add-picture {
    position: absolute;
}

.quick-contact {
    height: $height-quick-contact;
    .polygons {
        height: $height-quick-contact;
        .polygon {
            &.plg-1 {
                clip-path: polygon(0% 0%, calc(((100vw - $resolution) / 2) + $l-pt-quick-contact) 0%, calc(((100vw - $resolution) / 2) + $l-pt-quick-contact + $height-quick-contact) 100%, 0% 100%);
            }
        }
    }
    .qc-content {
        position: absolute;
        height: $height-quick-contact;
        padding-left: 600px;
        display: flex;
        align-items: center;
        .facebook {
            width: 130px;
            height: 130px;
            border-radius: 50%;
            background-color: white;
            display: flex;
            justify-content: center;
            align-items: center;
            img {
                height: 60%;
            }
        }
        .tel {
            margin-left: 300px;
            padding-left: 40px;
            border-left: 5px solid white;
            color: white;
            p {
                font-size: 38px;
                font-weight: 600;
            }
            a {
                p {
                    font-size: 50px;
                    font-weight: 600;
                    color: white;
                    span {
                        font-weight: 200;
                    }
                }
            }
        }
    }
}

footer {
    position: absolute;
    left: 0;
    width: 100%;
    height: 450px;
    margin-top: 50px;
    font-weight: 400;
    border-top: 4px solid black;
    .foot-content {
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        width: $resolution;
        height: 100%;
        z-index: 2;
        padding: 0 200px;
        display: flex;
        justify-content: space-around;
        align-items: center;
        .left-box {
            width: 1200px;
            height: 230px;
            display: flex;
            justify-content: space-between;
            .footer-logo {
                img {
                    width: 500px;
                }
            }
            .wrapper {
                display: flex;
                .company-box {
                    padding: 0 30px;
                    border-left: 5px solid black;
                    font-size: 26px;
                    font-weight: 400;
                }
                .address-box {
                    font-size: 28px;
                    font-weight: 400;
                }
                .company-box p,
                .address-box p {
                    line-height: 1.5em;
                }
            }
            
            
        }
        .right-box {
            width: 540px;
            ul {
                height: 230px;
                display: flex;
                flex-direction: column;
                flex-wrap: wrap;
                li {
                    display: inline-block;
                    margin: 8px 0;
                    padding-left: 30px;
                    border-left: 4px solid black;
                    font-size: 28px;
                    font-weight: 600;
                    
                    a {
                        color: black;
                    }
                }
            }
        }
    }
}

