                        clip-path: polygon(0% 0%, calc(((100vw - $resolution) / 2) + $r1-pt-contact-1) 0%, calc(((100vw - $resolution) / 2) + $r1-pt-contact-1 - $height-sec-1) 100%, 0% 100%);
                    }
                    &.plg-2 {
                        clip-path: polygon(calc(100% + $l1-pt-contact-2 - ((100vw - $resolution) / 2)) 0%, 100% 0%, 100% 100%, calc(100% + $l1-pt-contact-2 - $height-sec-1 - ((100vw - $resolution) / 2)) 100%);
                    }
                }
                &.left {
                    filter: drop-shadow(5px 0px 15px rgba(0, 0, 0, 0.6));
                }
                &.right {
                    filter: drop-shadow(0px 0px 0px rgba(0, 0, 0, 0.5));
                }
            }
        }
        .content {
            padding-left: 60px;
            padding-top: 60px;
            .line {
                padding-left: 30px;
                border-left: 2px solid black;
                .text {
                    padding-right: 510px;
                }
                img {
                    height: 280px;
                }
            }
        }
    }
    
    .contact-2 {
