    /* LIGHTBOX */
    
    #lightbox {
        .img-wrapper {
            .close-btn {
                top: 20px;
                right: -70px;
                padding: 10px;
                img {
                    width: 32px;
                }
            }
            .prev-btn {
                left: -100px;
                padding: 30px;
                img {
                    width: 45px;
                }
            }
            .next-btn {
                right: -100px;
                padding: 30px;
                img {
                    width: 45px;
                }
            }
            .img-title {
                p {
                font-size: 20px;
                }
            }
        }
        .img-wrapper > img {
            padding: 100px 0 10px 0;
            max-width: 75vw;
            max-height: 85vh;
            
        }
